@tailwind base;
@tailwind components;
@tailwind utilities;
@import url("https://cdn.jsdelivr.net/gh/orioncactus/pretendard@v1.3.9/dist/web/static/pretendard.min.css");

:root {
  font-family: "Pretendard Variable", Pretendard, -apple-system,
    BlinkMacSystemFont, system-ui, Roboto, "Helvetica Neue", "Segoe UI",
    "Apple SD Gothic Neo", "Noto Sans KR", "Malgun Gothic", "Apple Color Emoji",
    "Segoe UI Emoji", "Segoe UI Symbol", sans-serif;
}

.material-icons {
  font-family: "Material Icons";
  display: inline-block;
}

h1 {
  @apply text-5xl font-extrabold text-black text-left py-4 break-keep;
}

h2 {
  @apply text-4xl font-bold text-black whitespace-pre-line py-2;
}

h3 {
  @apply text-xl font-normal text-black whitespace-pre-line leading-relaxed py-2 break-keep;
}

h4 {
  @apply text-2xl font-extrabold whitespace-pre-line text-black py-2;
}

h5 {
  @apply text-xl font-bold whitespace-pre-line text-orange-600 py-2;
}

p {
  @apply text-xl font-medium text-black leading-relaxed whitespace-pre-line py-1 break-keep;
}

.p-bold {
  @apply font-bold;
}

.p-light {
  @apply font-light;
}

.p-trun {
  @apply line-clamp-2 text-base font-normal;
}

.p-small {
  @apply text-lg text-neutral-500 font-normal;
}

.button-bold {
  @apply px-5 py-3 my-4 text-xl font-bold text-white transition duration-500 ease-in-out bg-orange-500 border-2 border-orange-500 rounded-xl hover:bg-white hover:text-orange-500 hover:border-orange-500 hover:shadow-lg hover:shadow-orange-200 hover:transition-shadow;
}

.list-light {
  @apply list-disc text-lg font-light space-y-2;
}

.button-tag {
  @apply inline-block border-2 text-lg font-semibold px-5 py-1 rounded-full text-black text-center border-black hover:shadow-lg transition-shadow m-1;
}

.maintext1 {
  @apply space-y-4;
}

.section-main {
  @apply w-full py-10;
}

.gradient-light-orange {
  @apply bg-gradient-to-tr from-orange-200 to-orange-50;
}

.section-main-container {
  @apply max-w-[1200px] mx-auto my-10;
}

.title-container {
  @apply pb-6;
}

.grid-container {
  @apply flex-row divide-y divide-neutral-400 space-y-4;
}

.grid-container > .row {
  @apply flex overflow-auto py-4;
}

@media (max-width: 640px) {
  .section-main-container {
    @apply px-4 my-2;
  }

  .grid-container > .row {
    @apply flex-col;
  }
}

.grid-container > .row h5 {
  @apply w-[50px];
}

.grid-container > .row h2 {
  @apply w-[300px];
}

.grid-container > .row p {
  @apply flex-1;
}

/* flex-container: 모바일에서는 flex-col */

.flex-container {
  @apply flex flex-col space-y-4 space-x-0 sm:flex-row sm:space-x-8 sm:space-y-0;
}

.card-container {
  display: flex;
  flex-flow: row-reverse;
  flex-wrap: wrap;
}

.history-container > .flex-container > h5 {
  @apply text-2xl pb-0 pt-4 md:pt-3;
}

.history-container h5 {
  @apply py-3 leading-normal w-12 px-4 md:w-16 text-center text-lg;
}

.history-container p {
  @apply py-3 leading-normal font-normal text-base md:py-3 md:text-lg;
}

.history-container .year-container {
  @apply top-0 flex flex-col-reverse h-full mx-4 border-l-2 border-orange-500 pb-2 md:pb-8;
}

.history-container .content-container {
  @apply flex-col;
}

.box-rounded {
  @apply px-4 pt-3 pb-6 space-y-2 border rounded-2xl border-neutral-300 bg-neutral-100 sm:pb-8 sm:px-8 sm:pt-4;
}

.box-dropdown {
  @apply absolute z-10 flex flex-col invisible px-8 py-8 space-y-6 text-lg transition-all duration-300 ease-out transform -translate-y-2 shadow-lg opacity-0 mt-7 bg-neutral-100 group-hover:opacity-100 group-hover:visible group-hover:translate-y-0;
}

.hover-orange-text {
  @apply hover:text-orange-600 whitespace-nowrap;
}

@media (max-width: 768px) {
  h1 {
    @apply text-4xl py-2;
  }
  h2 {
    @apply text-2xl py-2;
  }
  h3 {
    @apply text-lg leading-relaxed py-1;
  }
  h5 {
    @apply text-base py-1;
  }

  p {
    @apply text-base;
  }

  .button-tag {
    @apply px-3 text-sm;
  }

  .list-light {
    @apply text-base;
  }
}
